
function NFTCard({cardName}) {

    return (<>
            {/* <div class="card" onclick="getNftDetails('12 The Ego','3500 <font color=white>discontinued</font>','3500','')" data-toggle="modal" data-target="#nftDetails"> */}
            <div className="card" data-toggle="modal" data-target="#nftDetails" style={{width:'185px'}}>
                <div className="card-image">
                    <img src={"https://www.risingstargame.com/images/cards/"+cardName+".png"} alt={cardName} className="img-fluid gray-image card-img" />
                </div>
            </div>
        </>);
}

export default NFTCard;